@charset "UTF-8";
.roadmapProgressLine, .roadmapProgressLine--finished, .roadmapProgressLine_current {
  display: block;
  background-color: white;
  z-index: 3; }

.roadmapProgressLine_line {
  border-left: 2px dashed #dee3eb;
  height: calc(100% - 47px);
  margin: 0 22.5px; }

.roadmapProgressLine_circle {
  width: 16px;
  height: 16px;
  margin: 15.5px;
  background: #dee3eb;
  border-radius: 50%; }

.roadmapProgressLine--finished .roadmapProgressLine_line {
  border-left: 2px solid rgba(54, 158, 207, 0.4); }

.roadmapProgressLine--finished .roadmapProgressLine_circle {
  background: #369ecf;
  width: 24px;
  height: 24px;
  margin: 11.5px; }
  .roadmapProgressLine--finished .roadmapProgressLine_circle:before {
    content: "✓";
    position: relative;
    color: white;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(1.2); }

.roadmapProgressLine_current .roadmapProgressLine_line {
  border-left: 2px solid #369ecf; }

.roadmapProgressLine_current .roadmapProgressLine_circle {
  background: #369ecf; }
  .roadmapProgressLine_current .roadmapProgressLine_circle:before {
    content: "";
    display: block;
    position: relative;
    width: 24px;
    height: 24px;
    border: 2px solid #369ecf;
    bottom: 6px;
    right: 6px;
    border-radius: 50%; }
